import React, { useEffect, useState } from "react";
import { Link, Navigate, useNavigate } from "react-router-dom";
import MyButton from "../components/UI/button/MyButton";
import MySubmit from "../components/UI/button/MySubmit";
import MyInput from "../components/UI/input/MyInput";
import MySelect from "../components/UI/select/MySelect";
import TextHeader from "../components/UI/textHeader/TextHeader";
import MyDisabledSubmit from "../components/UI/button/MyDisabledSubmit";
import { useDispatch, useSelector } from "react-redux";
import AuthService from "../services/AuthService";
import Loader from "../components/UI/Loader/Loader";
import WhiteHeader from "../components/UI/textHeader/WhiteHeader";
import { Utils } from "../utils/utils";

export default function Registration() {
  const dispatch = useDispatch();
  const isAuth = useSelector((state) => state.isAuth.isAuth);
  const [isLoading, setIsLoading] = useState(false);
  const [login, setLogin] = useState("");
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [connectionEmail, setConnectionEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [regions, setRegions] = useState([
    { value: "", key: 1 },
    { value: "", key: 2 },
    { value: "", key: 3 },
    { value: "", key: 4 },
    { value: "", key: 5 },
    { value: "", key: 6 },
    { value: "", key: 7 },
    { value: " ", key: 8 },
    { value: "", key: 9 },
  ]);
  const [region, setRegion] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [formValid, setFormValid] = useState(false);
  const [loginError, setLoginError] = useState("Email-    ");
  const [passwordError, setPasswordError] = useState(
    "    "
  );
  const [confirmPasswordError, setConfirmPasswordError] = useState(true);
  const [firstNameError, setFirstNameError] = useState(" ");
  const [lastNameError, setLastNameError] = useState(" ");
  const [phoneError, setPhoneError] = useState(
    "  099999999"
  );
  const [regionError, setRegionError] = useState(" ");
  const [connectionEmailError, setConnectionEmailError] = useState(
    "Email  "
  );
  const navigate = useNavigate();

  useEffect(() => {
    if (
      loginError ||
      passwordError ||
      confirmPasswordError ||
      regionError ||
      firstNameError ||
      lastNameError ||
      regionError ||
      phoneError ||
      connectionEmailError
    ) {
      setFormValid(false);
    } else {
      setFormValid(true);
    }
  }, [
    loginError,
    passwordError,
    confirmPasswordError,
    regionError,
    firstNameError,
    lastNameError,
    connectionEmailError,
    phoneError,
  ]);

  const loginHandler = (e) => {
    setLogin(e.target.value);
    const re =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (!re.test(String(e.target.value).toLowerCase())) {
      setLoginError(" email");
    } else {
      setLoginError("");
    }
  };

  const passwordHandler = (e) => {
    setPassword(e.target.value);
    if (e.target.value.length < 6 || e.target.value.length > 15) {
      setPasswordError("     6-   15-");
      if (!e.target.value) {
        setPasswordError("    ");
      }
    } else {
      setPasswordError("");
    }
  };
  const confirmPasswordHandler = (e) => {
    setConfirmPassword(e.target.value);
    if (e.target.value !== password) {
      setConfirmPasswordError(true);
    } else {
      setConfirmPasswordError(false);
    }
  };

  const phoneHandler = (e) => {
    setPhone(e.target.value);
    if (e.target.value.length < 9 || e.target.value.length > 9) {
      setPhoneError("9 ");
      if (!e.target.value) {
        setPhoneError("    ");
      }
    } else {
      setPhoneError("");
    }
  };
  const firstNameHandler = (e) => {
    setFirstName(e.target.value);
    if (!e.target.value) {
      setFirstNameError(" ");
    } else {
      setFirstNameError("");
    }
  };
  const lastNameHandler = (e) => {
    setLastName(e.target.value);
    if (!e.target.value) {
      setLastNameError(" ");
    } else {
      setLastNameError("");
    }
  };
  const connectionEmailHandler = (e) => {
    setConnectionEmail(e.target.value);
    const re =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (!re.test(String(e.target.value).toLowerCase())) {
      setConnectionEmailError(" email");
    } else {
      setConnectionEmailError("");
    }
  };

  const Registration = async (event) => {
    setIsLoading(true);
    event.preventDefault();
    const date = Utils.getMyDate;
    const response = await AuthService.registration(
      login,
      password,
      firstName,
      lastName,
      region,
      connectionEmail,
      phone,
      date
    );
    console.log(response.data);
    localStorage.setItem("token", response.data.accessToken);
    dispatch({ type: "SAVE_USER", payload: response.data.user });
    dispatch({ type: "CHANGE_AUTH", payload: !isAuth });
    setIsLoading(false);
    navigate("/home");
  };

  return isLoading ? (
    <Loader />
  ) : (
    <div className="main">
      <form onSubmit={Registration} className="registration_form">
        <div className="registration">
          <div className="registration_container1">
            <WhiteHeader text="" />

            <div className="reg_container">
              <div>
                <TextHeader text="" />
              </div>
            </div>

            <div className="reg_container">
              <MyInput
                value={login}
                inputname="Email"
                onChange={(e) => loginHandler(e)}
                type="text"
                placeholder="  email-..."
              />
              <div className="input_error">{loginError}</div>
            </div>
            <div className="reg_container">
              <MyInput
                value={password}
                inputname=""
                onChange={(e) => passwordHandler(e)}
                type="password"
                placeholder="  ..."
              />
              <div className="input_error">{passwordError}</div>
            </div>
            <div className="reg_container_icon">
              <div style={{ width: "70%", height: "100%" }}>
                <MyInput
                  value={confirmPassword}
                  inputname=" "
                  onChange={(e) => confirmPasswordHandler(e)}
                  type="password"
                  placeholder="  ..."
                />
              </div>

              <div
                style={{
                  height: "55%",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                {" "}
                {confirmPasswordError ? (
                  <img
                    src="/img/remove.svg"
                    width={"80%"}
                    height={"50%"}
                    alt="checked"
                  />
                ) : (
                  <img
                    src="/img/check.svg"
                    width={"80%"}
                    height={"50%"}
                    alt="checked"
                  />
                )}
              </div>
            </div>
            <div className="reg_container">
              <MySelect
                options={regions}
                defaultValue=""
                setRegionError={setRegionError}
                value={region}
                setValue={setRegion}
              />
              <div className="input_error">{regionError}</div>
            </div>
          </div>
          <div className="registration_container2">
            <div className="reg_container"></div>
            <div style={{ display: "flex", width: "100%", height: "20%" }}>
              <div className="reg_container_name">
                <MyInput
                  value={firstName}
                  inputname=""
                  onChange={(e) => firstNameHandler(e)}
                  type="text"
                  placeholder=" ..."
                />
                <div className="input_error">{firstNameError}</div>
              </div>
              <div className="reg_container_last_name">
                <MyInput
                  value={lastName}
                  inputname=""
                  onChange={(e) => lastNameHandler(e)}
                  type="text"
                  placeholder=" ..."
                />
                <div className="input_error">{lastNameError}</div>
              </div>
            </div>

            <div className="reg_container">
              <MyInput
                value={connectionEmail}
                inputname="Email  "
                onChange={(e) => connectionEmailHandler(e)}
                type="text"
                placeholder=" email  ..."
              />
              <div className="input_error">{connectionEmailError}</div>
            </div>
            <div className="reg_container">
              <MyInput
                value={phone}
                inputname=" "
                onChange={(e) => phoneHandler(e)}
                type="text"
                placeholder=" ..."
              />
              <div className="input_error">{phoneError}</div>
            </div>
            <div className="reg_to_log">
              {formValid ? (
                <div className="reg_submit_container">
                  <MySubmit onSubmit={Registration} type="submit">
                    
                  </MySubmit>
                </div>
              ) : (
                <div className="reg_submit_container">
                  <MyDisabledSubmit></MyDisabledSubmit>
                </div>
              )}

              <div className="login_registration">
                <h6 style={{ color: "green", marginRight: "10px" }}>
                   
                </h6>
                <div className="go_to_log_reg">
                  <Link to={"/login"}>
                    <MyButton></MyButton>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}
